.auth-main {
  background-image: url(../../images/algeria.png);
  color: #eee;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 1000px;
  perspective-origin: 50% 50%;
  animation: animation 800s linear infinite;
}

@keyframes animation {
  100% {
    background-position: 0px -3000px;
  }
}

.auth-content {
  left: 50%;
  padding: 10vmin;
  position: fixed;
  text-align: center;
  top: 48%;
  transform: translate(-50%, -50%);
}

.auth-card {
  text-align: center;
}
.auth-logo {
  width: 217px;
  margin-bottom: -32px;
  padding-bottom: 2rem;
}
.auth-form {
  margin-top: 35px;
}
.auth-input-field {
  font-size: 18px;
  margin-bottom: 8px;
  justify-content: center;
  display: flex;
}
.head-title {
  color: #2185d0;
  font-size: 2rem;
  font-weight: 900;
  font-family: monospace;
}
.ui.fluid.input {
  display: flex;
  width: 277px;
}
.ui.primary.button {
  box-shadow: 0px 0em 0px 0px rgba(34, 36, 38, 0.15) inset;
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}
.ui.huge.fluid {
  margin-top: 16px;
}
.ui.icon.input {
  color: #2185d0;
}
.carousel-container img {
  width: 43% !important;
  display: block !important;
  margin-left: auto !important;
  margin-right: auto !important;
  border: 14px solid #fff;
  border-radius: 6px;
}
.carousel-container {
  background-color: #f1f2f6 !important;
}
.carousel {
  height: 660px !important;
}
